import { Activity, Calendar, Mail, MessageSquare } from 'lucide-react';
import { StatCard } from '../components/StatCard';
import { Campaign } from './campaign';
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Legend } from 'recharts';

interface DashboardProps {
  campaigns: Campaign[];
}

export function Dashboard({ campaigns }: DashboardProps) {
  const activeCampaigns = campaigns.filter(c => c.status === 'Active').length;
  const totalSent = campaigns.reduce((sum, c) => sum + c.sent, 0);
  const totalReplies = campaigns.reduce((sum, c) => sum + c.replies, 0);
  const meetingsBooked = Math.floor(totalReplies * 0.28);

  const chartData = campaigns
    .filter(c => c.status !== 'Draft')
    .slice(0, 6)
    .map(c => ({
      name: c.name.length > 15 ? c.name.substring(0, 15) + '...' : c.name,
      sent: c.sent,
      replies: c.replies,
    }));

  return (
    <div className="p-4 sm:p-6 lg:p-8">
      <div className="mb-6 sm:mb-8">
        <h1 className="text-2xl sm:text-3xl font-bold text-slate-900 mb-2">Dashboard</h1>
        <p className="text-sm sm:text-base text-slate-600">Campaign overview</p>
      </div>

      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4 sm:gap-6 mb-6 sm:mb-8">
        <StatCard
          title="Active Campaigns"
          value={activeCampaigns}
          icon={Activity}
        />
        <StatCard
          title="Emails Sent"
          value={totalSent.toLocaleString()}
          icon={Mail}
        />
        <StatCard
          title="Replies"
          value={totalReplies.toLocaleString()}
          icon={MessageSquare}
        />
        <StatCard
          title="Meetings Booked"
          value={meetingsBooked}
          icon={Calendar}
        />
      </div>

      <div className="bg-white rounded-xl shadow-sm border border-slate-200 p-4 sm:p-6">
        <h2 className="text-lg sm:text-xl font-bold text-slate-900 mb-4 sm:mb-6">Campaign Performance</h2>
        <div className="w-full h-[250px] sm:h-[300px]">
          <ResponsiveContainer width="100%" height="100%">
          <BarChart data={chartData}>
            <CartesianGrid strokeDasharray="3 3" stroke="#e2e8f0" />
            <XAxis
              dataKey="name"
              tick={{ fill: '#64748b', fontSize: 12 }}
              stroke="#cbd5e1"
            />
            <YAxis
              tick={{ fill: '#64748b', fontSize: 12 }}
              stroke="#cbd5e1"
            />
            <Tooltip
              contentStyle={{
                backgroundColor: '#1e293b',
                border: 'none',
                borderRadius: '8px',
                color: '#fff',
              }}
            />
            <Legend
              wrapperStyle={{ paddingTop: '20px' }}
              iconType="circle"
            />
            <Bar dataKey="sent" fill="#3b82f6" radius={[8, 8, 0, 0]} name="Sent" />
            <Bar dataKey="replies" fill="#10b981" radius={[8, 8, 0, 0]} name="Replies" />
          </BarChart>
          </ResponsiveContainer>
        </div>
      </div>
    </div>
  );
}
